body {
  overflow-x: hidden;
}
.hidden-thing {
  position: absolute;
  left: 100%;
  width: 50px;
  height: 50px;
  opacity: 0;
}

.navbar-custom {
  background-color: rgba(148,187,233,1);
} 

.card-intro {
  border: 0px;
  background: rgb(238,174,202);
  background: linear-gradient(360deg, rgba(148,187,233,0) 0%, rgba(148,187,233,1) 80%);

}
/* ON ID */

#welcome {
  font-weight: bold;
  font-size: 45px;
  color: #17385f;
  font-family: 'Ubuntu', sans-serif;
}

#welcomeMessage {
  font-size: 24px;
  color: #17385f;
  font-family: 'Ubuntu', sans-serif;
}

.adminHeader {
  font-size: 24px;
  font-weight: bold;
  padding-bottom: 5px;
}

.moreInfo {
  font-size: 16px;
}

#dashboardTitle {
  font-weight: bold;
  font-size: 40px;
  color: #17385f;
}

#inspirationalText {
  margin: 0px;
  padding: 0px;
}

#callToAction {
  margin: 10px;
  padding: 10px;
}

footer {
  background: rgb(238,174,202);
  background: linear-gradient(180deg, rgba(238,174,202,0) 0%, rgba(238,174,202,0.9) 67%);
}

a {
  color: grey;
}

a.bottomLink {
  color: black;
  font-size: 16px;
  padding: 5px;
}

p {
  font-size: 18px;
}

.nav-link {
  font-size: 20px;

}

.footer-copyright {
  background: rgba(238,174,202,0.9);
  font-size: 20px;
}